 * @Author: lbt666 '1319451895@qq.com'
 * @Date: 2022-08-05 15:23:01
 * @LastEditors: lbt666 '1319451895@qq.com'
 * @LastEditTime: 2022-10-24 11:47:03
 * @Description: 
-->
<template>
  <div class='main'>
    <heads save="save"
           @previousStep="previousStep" />
    <div class="info">
      <div class="title">完善基本信息</div>
      <div class="tips">让最适合的人才快速找到你</div>
    </div>
    <van-form @submit="onSubmit"
              class="form"
              :show-error-message="false"
              validate-trigger="onSubmit"
              validate-first
              scroll-to-error>
      <van-field v-model="form.title"
                 name="title"
                 label="需求标题（必填）"
                 placeholder="请输入需求标题"
                 :rules="[{ required: true }]"
                 maxlength="15" />
      <van-field class="textarea"
                 v-model="form.describe"
                 type="textarea"
                 rows="3"
                 autosize
                 maxlength="200"
                 show-word-limit
                 name="describe"
                 label="需求描述（必填）"
                 placeholder="请输入需求描述"
                 :rules="[{ required: true }]" />

      <van-field class="radio"
                 name="demandType"
                 label="需求类型（必填）"
                 :rules="[{ required: true }]">
        <template #input>
          <van-radio-group v-model="form.demandType"
                           direction="horizontal">
            <van-radio name="1">
              <template #icon="props">
                <div class="demandType"
                     :class="props.checked ? 'active' : ''">代码审计</div>
              </template>
            </van-radio>
            <van-radio name="2">
              <template #icon="props">
                <div class="demandType"
                     :class="props.checked ? 'active' : ''">讲师招募</div>
              </template>
            </van-radio>
            <van-radio name="3">
              <template #icon="props">
                <div class="demandType"
                     :class="props.checked ? 'active' : ''">智能合约</div>
              </template>
            </van-radio>
          </van-radio-group>
        </template>
      </van-field>
      <van-field class="bond"
                 v-model="form.bond"
                 name="bond"
                 type="digit"
                 label="项目保证金（必填）"
                 placeholder="项目保证金不能为0且最高不能超过项目款"
                 :rules="[{ validator: asyncpProjectFunds, name: 'bond' }]"
                 @input="userBond"
                 maxlength="5"
                 :disabled="win_user_id > 0 ? true : false">
        <template #button>
          <div class="sele"
               @click="choose('bond')">
            <div class="coin">{{ form.bondType }}</div>
            <div class="downIcon">
              <img src="@/assets/downIcon.png"
                   alt="">
            </div>
          </div>
        </template>
      </van-field>
      <van-field class="radio"
                 name="paymentMethod"
                 label="项目付款方式（必填）"
                 :rules="[{ required: true }]">
        <template #input>
          <van-radio-group v-model="form.paymentMethod"
                           direction="horizontal">
            <!-- <van-radio name="0">
              <template #icon="props">
                <div class="demandType" :class="props.checked ? 'active' : ''">首付50%，中期30%，尾款20%</div>
              </template>
            </van-radio> -->
            <van-radio name="1">
              <template #icon="props">
                <div class="demandType"
                     :class="props.checked ? 'active' : ''">一次付清</div>
              </template>
            </van-radio>
          </van-radio-group>
        </template>
      </van-field>
      <van-field class="bond"
                 v-model="form.projectFunds"
                 type="digit"
                 name="projectFunds"
                 label="项目款（必填）"
                 placeholder="请输入项目款"
                 :rules="[{
          validator: asyncpProjectFunds, name: 'projectFunds'
        }]"
                 maxlength="5"
                 @input="form.negotiable = false"
                 :disabled="win_user_id > 0 ? true : false">
        <template #button>
          <div class=" projectFunds">
            <div class="sele"
                 @click="choose('projectFunds')">
              <div class="coin">{{ form.bondType }}</div>
              <div class="downIcon">
                <img src="@/assets/downIcon.png"
                     alt="">
              </div>
            </div>
            <div class="btnStyle">
              <van-button native-type="button"
                          class="negotiable"
                          :plain="!form.negotiable"
                          :disabled="win_user_id > 0 ? true : false"
                          @click="form.negotiable = !form.negotiable; form.projectFunds = ''"
                          size="small"
                          type="primary"
                          color="#1F75D9">面议
              </van-button>
            </div>
          </div>
        </template>
      </van-field>
      <van-field class="bond"
                 v-model="form.number"
                 name="number"
                 type="digit"
                 label="允许报名人数（必填）"
                 placeholder="请输入报名人数"
                 :rules="[{ validator: asyncpProjectFunds, name: 'number' }]"
                 maxlength="4"
                 @input="form.unlimited = false">
        <template #button>
          <div class="btnStyle">
            <van-button native-type="button"
                        class="negotiable"
                        :plain="!form.unlimited"
                        @click="form.unlimited = !form.unlimited; form.number = ''"
                        size="small"
                        type="primary"
                        color="#1F75D9">不限
            </van-button>
          </div>
        </template>
      </van-field>
      <van-field readonly
                 clickable
                 name="registrationTime"
                 :value="form.registrationTime"
                 label="报名时间（必填）"
                 placeholder="报名截止时间"
                 @click="showCalendar = true; timeType = 0"
                 :rules="[{ validator: asyncDate, name: 'registrationTime' }]" />
      <van-field readonly
                 clickable
                 name="deliveryTime"
                 :value="form.deliveryTime"
                 label="交付时间（必填）"
                 placeholder="交付截止时间"
                 @click="showCalendar = true; timeType = 1"
                 :rules="[{ validator: asyncDate, name: 'deliveryTime' }]" />
      <div class="btns">
        <van-button type="info"
                    class="btn"
                    native-type="button"
                    color="#1F75D9"
                    plain
                    @click="previousStep">
          {{ query.status ? '取消修改' : '上一步' }}
        </van-button>
        <van-button type="info"
                    class="btn"
                    native-type="submit"
                    color="#1F75D9">{{ query.status ? '保存并退出' : '提交' }}
        </van-button>
      </div>
    </van-form>
    <van-action-sheet v-model="currencyShow"
                      cancel-text="取消"
                      close-on-click-action
                      :actions="actions"
                      @select="onSelect" />
    <van-calendar class="calendar"
                  v-model="showCalendar"
                  @confirm="onConfirm" />
  </div>
</template>
<script>
import { Dialog, Toast } from 'vant';
import { taskCreate, taskDetails, taskUpdate } from '@/api/request'

export default {
  data () {
    return {
      win_user_id: '',
      DateNow: new Date(new Date().toLocaleDateString()).getTime(),
      query: '',
      task_id: '',
      showCalendar: false,
      currencyShow: false,
      currencyType: '',
      actions: [
        {
          name: 'SRS'
        },
        {
          name: 'USDT'
        }
      ],
      form: {
        title: '',
        describe: '',
        demandType: '1',
        bondType: 'SRS',
        bond: '',
        paymentMethod: '1',
        projectFunds: '',
        projectFundsType: 'SRS',
        negotiable: false, // 是否面议
        number: '',
        unlimited: false, // 1
        registrationTime: '',
        deliveryTime: ''
      }
    }
  },
  methods: {
    choose (val) {
      if (this.win_user_id > 0) return
      this.currencyShow = true
      this.currencyType = val
    },
    userBond (val) {
      let num1 = +val
      let num2 = +this.form.projectFunds
      if (num1 && num2 && num1 > num2) {
        Toast('最高不能超过项目款')
        setTimeout(() => {
          this.form.bond = ''
        }, 300);
      }
    },
    onSelect (item) {
      this.form.bondType = item.name
      return
      if (this.currencyType === 'bond') {
        this.form.bondType = item.name
      } else if (this.currencyType === 'projectFunds') {
        this.form.projectFundsType = item.name
      }
      this.currencyShow = false;
    },
    onConfirm (date) {
      let Y = date.getFullYear()
      let M = date.getMonth() + 1 < 10 ? '0' + (date.getMonth() + 1) : (date.getMonth() + 1)
      let D = date.getDate() < 10 ? ('0' + date.getDate()) : date.getDate()
      if (this.timeType) {
        this.form.deliveryTime = `${Y}/${M}/${D}`;
      } else {
        this.form.registrationTime = `${Y}/${M}/${D}`;
      }
      this.showCalendar = false;
    },
    previousStep () {
      console.log(this.$options.data.call(this).form)
      let optionsData = this.$options.data.call(this).form
      let nowData = this.form
      if (JSON.stringify(optionsData) !== JSON.stringify(nowData) && !this.query.status) {
        // 不相等，表示有改动
        let parms = {
          confirmButtonText: '继续填写',
          cancelButtonText: '放弃填写',
          message: '现在离开将无法保存需求为你推荐优质人才,是否离开？'
        }
        this.showDialog(parms, () => { }, () => {
          // 缓存已填写数据
          localStorage.setItem('form', JSON.stringify(this.form))
          this.$router.go(-1)
          console.log('取消事件')
        })
      } else {
        this.$router.go(-1)
      }
    },
    async onSubmit (values) {
      if (this.win_user_id > 0) {
        Toast('中标后不可修改')
        return
      }
      let parms = {
        "allows": this.form.number ? +this.form.number : -1, // 最大人数
        "amount": this.form.projectFunds ? +this.form.projectFunds : -1, // 项目金额
        "bail": +this.form.bond, // 保证金
        "begin_time": new Date(this.form.registrationTime).getTime(), // 开始时间
        "delivery_time": new Date(this.form.deliveryTime).getTime(), // 交付时间
        "description": this.form.describe,//描述
        "pay_type": 1,// 支付类型（1  一次性支付）
        "amount_type": this.form.bondType === 'SRS' ? 1 : 2, // 1 srs 2 usdt
        "bail_type": this.form.bondType === 'SRS' ? 1 : 2, // 1 srs 2 usdt
        // "serial_id": 0, // 任务编号
        // "status": 0, // 状态
        "title": this.form.title, // 标题
        "type": +this.form.demandType, // 任务类型
        "user_id": Number(localStorage.getItem('userId'))  // 
      }
      if (this.task_id) {
        parms.id = +this.task_id
      }
      if (parms.bail > parms.amount && parms.amount != -1) {
        Toast('保证金不能超过项目款')
        return
      }
      if (this.DateNow > parms.begin_time) {
        Toast('报名时间不能晚于当前时间')
        return
      }
      let res = await (this.task_id ? taskUpdate : taskCreate)(parms)
      if (res.code === 200) {
        localStorage.removeItem('form')
        Toast('需求发布成功')
        this.$router.push({
          path: 'demandDetails',
          query: {
            id: res.data.id || this.task_id
          }
        })
      }
    },
    // 给项目保证金插入提示
    bondInsertTips () {
      let bond = document.getElementsByClassName('bond')
      let objE = document.createElement('div');
      objE.innerHTML = '* 保证金由雇主缴纳后，才可投递该项目';
      objE.className = 'bondTips';
      bond[0].appendChild(objE)
    },
    showDialog (parms, confirmFun, cancelFun) {
      Dialog.confirm({
        title: parms.title || '标题',
        message: parms.message,
        showCancelButton: true,
        confirmButtonText: parms.confirmButtonText,
        confirmButtonColor: '#1F75D9',
        cancelButtonText: parms.cancelButtonText
      })
        .then(() => {
          // on confirm
          confirmFun()
        })
        .catch(() => {
          // on cancel
          cancelFun()
        });
    },
    // 项目款/报名人数的异步校验
    asyncpProjectFunds (value, rule) {
      return new Promise((resolve) => {
        if (rule.name === 'projectFunds') {
          // 项目款
          if (this.form.negotiable) {
            // 选择面议
            resolve(true)
          } else {
            if (Number(value) === 0) {
              Toast('项目款不能为0')
            }
            resolve(Number(value) ? true : false)
          }
        } else if (rule.name === 'bond') {
          // 保证金
          if (Number(value) === 0) {
            Toast('项目保证金不能为0且最高不能超过项目款')
          }
          resolve(Number(value) ? true : false)
        } else {
          // 报名人数
          if (this.form.unlimited) {
            // 选择不限
            resolve(true)
          } else {
            if (Number(value) === 0) {
              Toast('报名人数不能为0')
            }
            resolve(Number(value) ? true : false)
          }
        }
      });
    },
    // 报名与交付事件的校验
    asyncDate (value, rule) {
      if (!value) return false
      let date = new Date(value)
      let timeChuo = date.getTime()
      return new Promise((resolve) => {
        if (rule.name === 'registrationTime') {
          // 报名时间
          if (this.form.deliveryTime) {
            let date_D = new Date(this.form.deliveryTime)
            let timeChuo_D = date_D.getTime()
            // 交付时间小于报名时间
            if (timeChuo < timeChuo_D) {
              resolve(true)
            } else {
              Toast('交付时间不能早于报名时间')
              resolve(false)
            }
          } else {
            resolve(true)
          }
        } else {
          //截止时间
          if (this.form.registrationTime) {
            // 存在报名时间
            let date_D = new Date(this.form.registrationTime)
            let timeChuo_D = date_D.getTime()
            if (timeChuo_D < timeChuo) {
              resolve(true)
            } else {
              Toast('交付时间不能早于报名时间')
              resolve(false)
            }
          } else {
            resolve(true)
          }
        }
      });
    },
    async getData (id) {
      let res = await taskDetails({
        task_id: id
      })
      if (res.data.amount === -1) {
        res.data.amount = ''
      }
      if (res.data.allows === -1) {
        res.data.allows = ''
      }
      let form = {
        title: res.data.title,
        describe: res.data.description,
        demandType: res.data.type.toString(),
        bondType: res.data.amount_type == 1 ? 'SRS' : 'USDT',
        bond: res.data.bail,
        paymentMethod: '1',
        projectFunds: res.data.amount,
        projectFundsType: res.data.amount_type == 1 ? 'SRS' : 'USDT',
        negotiable: res.data.amount === '' ? true : false, // 是否面议
        number: res.data.allows,
        unlimited: res.data.allows === '' ? true : false, // 1
        registrationTime: this.$util.getUnixToDate('yyyy-mm-dd', res.data.begin_time, '/'),
        deliveryTime: this.$util.getUnixToDate('yyyy-mm-dd', res.data.delivery_time, '/')
      }
      this.win_user_id = res.data.win_user_id
      this.form = form
    }
  },
  async mounted () {
    this.query = this.$route.query
    this.bondInsertTips()
    this.task_id = this.$route.query.id
    if (this.task_id) {
      await this.getData(this.task_id)
    } else {
      let res = localStorage.getItem('form')
      if (res) {
        this.form = JSON.parse(res)
        this.form.title = JSON.parse(res) ? JSON.parse(res).title : ''
      } else {
        this.form.title = this.$route.query.desc || ''
      }
    }
  }
}
</script>
<style lang='less' scoped>
.main {
  .calendar {
    padding-bottom: 20px;

    :deep(.van-calendar__selected-day),
    :deep(.van-calendar__confirm) {
      background-color: #1f75d9;
    }

    :deep(.van-calendar__confirm) {
      border-color: #1f75d9;
    }
  }

  .form {
    padding: 0 10px;

    .projectFunds {
      display: flex;
      align-items: center;
    }

    .btnStyle {
      margin-left: 5px;
    }

    .negotiable {
      width: 75px;
      height: 40px;
    }

    .btns {
      display: flex;
      justify-content: space-between;
      padding-top: 10px;
      margin-bottom: 17px;

      .btn {
        width: 175px;
      }
    }

    .bond {
      :deep(.bondTips) {
        margin-top: 12px;
        font-size: 12px;
        line-height: 1;
        font-weight: 400;
        color: #909cba;
      }

      :deep(.van-field__button) {
        padding-left: 0;
        display: flex;
        align-items: center;
      }

      .sele {
        height: 40px;
        background: #f6f6f6;
      }
    }

    .sele {
      display: flex;
      align-items: center;
      padding-right: 11px;

      .coin {
        font-size: 14px;
        margin-right: 7px;
        font-weight: 500;
        color: #333333;
      }

      .downIcon {
        width: 13px;
        height: 7px;
      }
    }

    :deep(.van-cell) {
      padding: 20px 10px;
      flex-direction: column;
      overflow: hidden;
      border-radius: 10px;
      margin-bottom: 5px;

      .van-cell__title {
        font-size: 14px;
        line-height: 1;
        font-weight: 500;
        color: #333333;
        margin-bottom: 14px;
        width: auto;
      }

      .van-cell__value {
        overflow: hidden;
        border-radius: 3px;

        input {
          height: 40px;
          padding: 0 11px;
          background: #f6f6f6;
        }

        textarea {
          padding: 16px 10px;
        }
      }
    }

    :deep(.van-cell::after) {
      border: none;
    }

    .textarea {
      :deep(.van-cell__value) {
        background: transparent;

        .van-field__body {
          background: #f6f6f6;
        }

        .van-field__word-limit {
          margin-top: 10px;
          font-size: 14px;
          font-weight: 400;
          color: #909cba;
        }
      }
    }

    .radio {
      :deep(.van-cell__value) {
        background: transparent;
      }
    }

    .radio {
      :deep(.van-radio__icon) {
        height: auto;
      }

      :deep(.van-radio.van-radio--horizontal) {
        margin-right: 0;
      }

      :deep(.van-radio-group) {
        flex: 1;
        justify-content: space-between;
      }
    }

    .demandType {
      height: 32px;
      border: 1px solid #1f75d9;
      border-radius: 16px;
      font-size: 14px;
      text-align: center;
      padding: 0 17px;
      line-height: 32px;
      font-weight: 400;
      color: #1f75d9;
    }

    .active {
      color: #ffffff;
      background: #1f75d9;
    }
  }

  .info {
    display: flex;
    flex-direction: column;
    padding: 20px;

    div {
      line-height: 1;
    }

    .title {
      font-size: 16px;
      font-weight: 500;
      color: #1f75d9;
    }

    .tips {
      font-size: 11px;
      margin-top: 6px;
      font-weight: 500;
      color: #1f75d9;
    }
  }
}
</style>